.transaction__history {
    color: black;
    width: 500px;
    margin: 30px auto;
    text-align: center;
  }
  
  th {
    border: 1px solid grey;
    background-color: #00bcd4;
    color: white;
    font-weight: 600;
    font-size: 15px;
    padding: 5px 0 5px 0;
  }
  td {
    border: 1px solid grey;
    font-weight: 300;
    font-size: 13px;
    padding: 5px 0 5px 0;
  }
  
  tr:nth-child(even) {
    background-color:rgb(190, 187, 187);
  }