.profile {
	width: 200px;
	text-align: center;
    margin: auto;
	border: solid 1px #d8e0ea;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
    border-radius: 0px 0px 4px 4px;
}

.name {
    text-align: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.875;
    letter-spacing: 0.03em;
}
.tag,
.location {
    color: #757575;
    cursor: pointer;
    font-family: "Roboto",serif;
    font-style: normal;
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.03em;
}
.stats {
    padding-left: 0;
    font-style: italic;
	list-style: none;
}